
import { TiLocale } from '@cloud/tiny3';
import { currencyEUR, dateHyphen, defaulten_US, defaultfr_FR, defaultzh_CN } from '@cloud/tinyplus3/locale/i18n';
import { siteConfig } from './siteConfig';

export class TpConfig {
    public static init(language: string = 'zh-CN'): void {
        const words: any = TiLocale.getWords();
        // 设置所有语言包
        TiLocale.setWords({
            'zh-CN': { ...words['zh-CN'], ...defaultzh_CN, ...dateHyphen, ...currencyEUR, ...siteConfig },
            'en-US': { ...words['en-US'], ...defaulten_US, ...dateHyphen, ...currencyEUR, ...siteConfig },
            'fr-FR': { ...words['fr-FR'], ...defaultfr_FR, ...dateHyphen, ...currencyEUR, ...siteConfig }
        });
        // 设置默认语言
        TiLocale.setLocale(language);
    }
}
